<template>
    <div class="menurollos">
        <div class="row row-cols-1 row-cols-md-2">
            <div v-for="rollos in rollos" :key="rollos.idRollos" class="col mb-4">
                <div class="card">
                    <img :src="rollos.imagen" class="card-img-top">
                    <div class="card-body">
                        <h5 class="card-title">{{rollos.nombre}}</h5>
                        <h3 class="card-text">{{rollos.precio}}</h3>
                        <p class="card-text"><b>Ingredientes:</b> {{rollos.ingredientes}}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'

const API_URI = 'http://132.145.37.186:8080/api';

export default {
    name: 'MenuRollos',
    data() {
        return {
            rollos:[]
        }
    },
    mounted() {
        let vue = this;
        axios.get(API_URI+'/rollos/listar').then(
            response => {
                vue.rollos = response.data;
                console.log(response.data);
                console.log(this.rollos);
        });
    },
    methods: {}
}
</script>