<template>
    <div class="contacto mt-4">
        <div class="card mb-3">
            <div class="row no-gutters">
                <div class="col-md-4">
                    <img src="/img/preguntas.png" class="img-fluid">
                </div>
                <div class="col-md-8">
                    <div class="card-body">
                        <h5 class="card-title"><b>¿Tienes preguntas?</b></h5>
                        <p class="card-text ikira-justificar">Para Ikira Sushi Roll es muy importante contar con las opiniones, comentarios y sugerencias de nuestros clientes.</p>
                        <p class="card-text ikira-justificar">Los invitamos a que nos escriban cualquier sugerencia y observación de nuestros productos.</p>
                    </div>
                </div>
            </div>
        </div>
        <form action="">
            <h4>Ingrese por favor los siguientes datos</h4>
            <div class="form-group">
                <label for="exampleInputNombre">Nombre completo</label>
                <input type="nombre" class="form-control" id="exampleInputNombre" v-model="datos.nombre" aria-describedby="emailHelp" required>
            </div>
            <div class="form-group">
                <label for="exampleInput">Correo electrónico</label>
                <input type="email" class="form-control" id="exampleInput" v-model="datos.email" placeholder="nombre@ejemplo.com" required>
            </div>
            <div class="form-group">
                <label for="exampleFormControlTextarea1">Sugerencia y observaciones</label>
                <textarea class="form-control" id="exampleFormControlTextarea1" v-model="datos.sugerencia" rows="3" required></textarea>
            </div>
            <button type="button" class="btn btn-primary" v-on:click="guardar()">Enviar</button>
        </form>
    </div>
</template>

<script>
import axios from 'axios'

const API_URI = 'http://132.145.37.186:8080/api';
export default {
    name: 'Contactanos',
    data:function() {
        return {
            datos:{
                nombre:"",
                email:"",
                sugerencia:"",
            }
        }
    },
    methods: {
        guardar() {
            axios.post(API_URI+'/contactanos',this.datos).then(
                data => {
                    console.log(data);
                }
            )
        }
    }
}
</script>
