<template>
    <div class="menuentradas">
        <div class="row row-cols-1 row-cols-md-2">
            <div v-for="entrada in entradas" :key="entrada.idEntrada" class="col mb-4">
                <div class="card">
                    <img :src="entrada.imagen" class="card-img-top">
                    <div class="card-body">
                        <h5 class="card-title">{{entrada.nombre}}</h5>
                        <h3 class="card-text">{{entrada.precio}}</h3>
                        <p class="card-text"><b>Ingredientes:</b> {{entrada.ingredientes}}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'

const API_URI = 'http://132.145.37.186:8080/api';

export default {
    name: 'MenuEntradas',
    data() {
        return {
            entradas:[]
        }
    },
    mounted() {
        let vue = this;
        axios.get(API_URI+'/entradas/listar').then(
            response => {
                vue.entradas = response.data;
                console.log(response.data);
                console.log(this.entradas);
        });
    },
    methods: {}
}
</script>