<template>
    <div class="menubebidas">
        <div class="row row-cols-1 row-cols-md-2">
            <div v-for="bebidas in bebidas" :key="bebidas.idBebidas" class="col mb-4">
                <div class="card">
                    <img :src="bebidas.imagen" class="card-img-top">
                    <div class="card-body">
                        <h5 class="card-title">{{bebidas.nombre}}</h5>
                        <h3 class="card-text">{{bebidas.precio}}</h3>
                        <p class="card-text"> {{bebidas.ingredientes}}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'

const API_URI = 'http://132.145.37.186:8080/api';

export default {
    name: 'MenuBebidas',
    data() {
        return {
            bebidas:[]
        }
    },
    mounted() {
        let vue = this;
        axios.get(API_URI+'/bebidas/listar').then(
            response => {
                vue.bebidas = response.data;
                console.log(response.data);
                console.log(this.bebidas);
        });
    },
    methods: {}
}
</script>