import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Nosotros from '../views/Nosotros.vue'
import Entradas from '../views/Entradas.vue'
import Bebidas from '../views/Bebidas.vue'
import Rollos from '../views/Rollos.vue'
import Contacto from '../views/Contacto.vue'
import Franquicia from '../views/Franquicia.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/nosotros',
    name: 'Quienes Somos',
    component: Nosotros
  },
  {
    path: '/menu/entradas',
    name: 'Menu Entradas',
    component: Entradas
  },
  {
    path: '/menu/bebidas',
    name: 'Menu Bebidas',
    component: Bebidas
  },
  {
    path: '/menu/rollos',
    name: 'Menu Rollos',
    component: Rollos
  },
  {
    path: '/contacto',
    name: 'Contactanos',
    component: Contacto
  },
  {
    path: '/franquicia',
    name: 'Franquicia',
    component: Franquicia
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
